:root {
  --font-ral: "Raleway", sans-serif;
  --color-blue: #08A6E4;
  --font-sans: "Open Sans", sans-serif;
  --color-black: #000000;
}

* {
  block-size: border-box;
}

body {
  width: auto;
  margin: 0 auto;
}

html, body {
  max-width: 100%;
  overflow-x: hidden;
}

.logo {
  text-decoration: none;
  color: var(--color-blue);
  font-family: var(--font-ral);
  font-size: 32px;
  font-weight: 300;
  margin-left: 136px;
}

.header {
  display: flex;
  justify-content: center;
  width: 100%;
  box-shadow: 0 3px 6px #00000029;
}
.header .header__container {
  position: relative;
  display: flex;
  align-items: center;
  justify-content: space-between;
  width: 100%;
  height: 65px;
}
.header .header__container .header__nav {
  display: flex;
  list-style: none;
  width: 100%;
  justify-content: space-between;
}
.header .header__container .header__nav .nav__link {
  text-decoration: none;
  color: #A5A5A5;
  padding: 28px 18px 22px;
  font-size: 11px;
  font-family: var(--font-sans);
  font-weight: bold;
  text-transform: uppercase;
  letter-spacing: 3.28px;
}

.nav__link:last-of-type {
  margin-right: 50px;
}

.nav__link:hover {
  color: var(--color-black);
  border-bottom: 4px solid var(--color-blue);
}

.burger {
  position: absolute;
  visibility: hidden;
  flex-direction: column;
  left: 93%;
  top: 44%;
}
.burger span {
  width: 20px;
  height: 2px;
  display: block;
  background: var(--color-blue);
  margin-bottom: 2px;
}

.burger__nav {
  position: relative;
  z-index: 999;
  list-style: none;
  justify-content: space-between;
  background: white;
  box-shadow: 0 3px 6px #00000029;
  height: 100%;
  padding-top: 10px;
  padding-bottom: 10px;
}
.burger__nav .burger__element {
  margin-bottom: 14px;
  width: 100%;
}
.burger__nav .burger__link {
  position: relative;
  text-decoration: none;
  color: #A5A5A5;
  padding: 28px 18px 22px;
  font-size: 11px;
  font-family: var(--font-sans);
  font-weight: bold;
  text-transform: uppercase;
  letter-spacing: 3.28px;
  margin-bottom: 14px;
}

.burger__menu {
  position: absolute;
  z-index: 99;
  width: 100%;
  height: 100%;
  top: 88%;
  visibility: hidden;
}

.burger__btn {
  position: absolute;
  width: 20px;
  opacity: 0;
}

.opening {
  display: flex;
  justify-content: center;
}

.opening__container {
  display: flex;
  flex-direction: column;
  margin-top: 130px;
}

.opening__title {
  display: flex;
  font-family: var(--font-ral);
  font-size: 49px;
  justify-content: center;
  margin-bottom: 20px;
}

.opening__text {
  display: flex;
  font-family: var(--font-sans);
  font-size: 16px;
  margin-top: 0px;
  margin-bottom: 38px;
  justify-content: center;
}

.opening__btn {
  display: flex;
  justify-content: center;
  text-decoration: none;
  font-weight: bold;
  color: white;
  border-radius: 40px;
  background-color: var(--color-blue);
  padding: 22px 155px;
}

.opening__image {
  width: 100%;
  height: 100%;
}

.background-laptop {
  background-image: url("../../assets/Background.png");
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
  height: 516px;
  width: 100%;
}

.third__img {
